export default {
	props: {		
		navTabs: {
			type: Array,
			default: function(){
				return [];
			}
		},
		navType: {
			type: [String, Number],
			default: 0
		}
	},
	data() {
		return {
			currentTab: 0
		}
	},
	methods:{
		switchTabs (nav){
			this.currentTab = nav.type;
			this.$emit('tabChange', nav, nav.type);
		}
	}
}
